import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Col, Row } from "reactstrap";
import {
  createUserServicesData,
  deleteUserServiceData,
  getUserServicesData,
} from "../reducers/serviceReducer";

import ProfileServiceSection from "./ProfileServiceSection";
import AnimatedElement from "./AnimatedElement";
import FileComponent from "./FileComponent";
import { useNavigate } from "react-router-dom";

const Services = (props) => {
  const { userdetails } = useSelector((state) => state.user);
  const displayType = props.displayType;
  const [serviceData, setServiceData] = useState(null);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const getUserService = async () => {
    const data = await dispatch(
      getUserServicesData({ token: userdetails.token })
    );
    if (!data.payload.error) {
      await setServiceData(data.payload);
    } else {
      await setServiceData(null);
    }
  };

  useEffect(() => {
    if (props.updateService) {
      getUserService();
    }
  }, [props.updateService]);

  const updateStatus = async (id, active) => {
    const data = await dispatch(
      createUserServicesData({
        id: id,
        active: active,
        token: userdetails.token,
      })
    );
    if (data) {
      let tempService = [...serviceData];
      await serviceData.forEach((item, i) => {
        if (item._id === data.payload._id) {
          tempService[i] = data.payload;
        }
      });
      setServiceData(tempService);
    }
  };

  const editServices = (sName, sHeading, sImage, sDesc, id) => {
    props.editServices(sName, sHeading, sImage, sDesc, id);
  };

  const deleteServices = async (id) => {
    const data = await dispatch(
      deleteUserServiceData({ id: id, token: userdetails.token })
    );
    getUserService();
  };
  return (
    <Row>
      {serviceData &&
        serviceData.map((item, i) =>
          displayType && displayType === "block" ? (
            <Col className={`col-12 col-sm-6 col-md-3 my-3`} key={item._id}>
              <AnimatedElement>
                <div
                  className="s-back text-center p-2"
                  onClick={(e) => navigate(`/services/${item._id}`)}
                >
                  {item.serviceImage ? (
                    <FileComponent
                      fileSrc={item.serviceImage}
                      fileClass="my-5"
                      alt={item.serviceName}
                    />
                  ) : (
                    ""
                  )}
                  <h3 className="mb-5 f-size-20">{item.serviceName}</h3>
                </div>
              </AnimatedElement>
            </Col>
          ) : (
            <Col
              className="col-12 service-item position-relative"
              key={item._id}
              style={{ overflowX: "hidden" }}
            >
              <ProfileServiceSection
                editServices={editServices}
                deleteServices={deleteServices}
                updateStatus={updateStatus}
                item={item}
              />
            </Col>
          )
        )}
    </Row>
  );
};

export default Services;
