import React, { Fragment, useEffect, useState } from "react";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalHeader,
  Row,
} from "reactstrap";
import EditorComponent from "./EditorComponent";
import FileInputComponent from "./FileInputComponent";

const FormElementComponent = (props) => {
  const formField = props.formField;
  const formInputValues = props.formInputValues;
  const [formData, setFormData] = useState({ ...formInputValues });
  const [errorMsg, setErrorMsg] = useState(null);
  const [file, setFile] = useState(null);

  const args = {
    backdrop: false,
    centered: true,
    size: "lg",
    fade: false,
  };

  const assignForm = async () => {
    let preformData = await formField.reduce((obj, key, index) => {
      obj[key.split("-")[0]] = formInputValues[key.split("-")[0]];
      return obj;
    }, {});

    setFormData({ ...formData, ...preformData });
  };

  const updateFileData = (fieldName, upDateData) => {
    setFile({ ...file, [fieldName]: upDateData });
  };

  const clickHandler = (e, btnType) => {
    e.preventDefault();
    if (btnType === "cancel") {
      props.closeModal();
    } else {
      let formValidate = true;
      formField.map((item, i) => {
        let itemData = item.split("-");
        let fieldValue =
          formData[Object.keys(formData).find((key) => key === itemData[0])];
        if (
          (fieldValue === undefined ||
            fieldValue === null ||
            fieldValue.toString().trim() === "") &&
          itemData[2] === "require"
        ) {
          formValidate = false;
        }
        return formValidate;
      });
      if (formValidate) {
        switch (btnType) {
          case "create":
            return props.createData(formData, file);
          case "update":
            return props.updateData(formData, file);
          default:
            return null;
        }
      } else {
        setErrorMsg("Please enter all required fields.");
      }
    }
  };

  const description = (value, fieldname) => {
    formData[fieldname] = value;
  };

  useEffect(() => {
    assignForm();
  }, [formInputValues]);

  return (
    <>
      {props.formType === "modal" ? (
        <Row>
          <Col>
            <Modal isOpen={true} style={{ pointerEvents: "none" }} {...args}>
              <ModalHeader>{props.modalHeading}</ModalHeader>
              <ModalBody>
                <Form>
                  {formField.map((item, i) => {
                    var itemData = item.split("-");
                    if (itemData[1] === "text") {
                      return (
                        <Fragment key={itemData[0]}>
                          <FormGroup className="px-3">
                            <Label
                              className="text-capitalize s-color"
                              for={itemData[0]}
                            >
                              {itemData[0].split(/(?=[A-Z])/).join(" ")}
                              {itemData[2] === "require" ? (
                                <span className="text-danger">*</span>
                              ) : (
                                ""
                              )}
                            </Label>
                            <Input
                              type="text"
                              id={itemData[0]}
                              placeholder={itemData[0]
                                .split(/(?=[A-Z])/)
                                .join(" ")}
                              required={
                                itemData[2] === "require" ? true : false
                              }
                              defaultValue={
                                formData[itemData[0]]
                                  ? formData[itemData[0]]
                                  : ""
                              }
                              onChange={(e) =>
                                (formData[e.target.id] = e.target.value)
                              }
                            />
                          </FormGroup>
                        </Fragment>
                      );
                    } else if (itemData[1] === "textarea") {
                      return (
                        <FormGroup key={itemData[0]} className="px-3">
                          <Label
                            className="text-capitalize s-color"
                            for={itemData[0]}
                          >
                            {itemData[0].split(/(?=[A-Z])/).join(" ")}
                            {itemData[2] === "require" ? (
                              <span className="text-danger">*</span>
                            ) : (
                              ""
                            )}
                          </Label>
                          <Input
                            type="textarea"
                            id={itemData[0]}
                            placeholder={itemData[0]
                              .split(/(?=[A-Z])/)
                              .join(" ")}
                            required={itemData[2] === "require" ? true : false}
                            defaultValue={
                              formData[itemData[0]] ? formData[itemData[0]] : ""
                            }
                            onChange={(e) =>
                              (formData[e.target.id] = e.target.value)
                            }
                          />
                        </FormGroup>
                      );
                    } else if (
                      itemData[1] === "quill" &&
                      formData[itemData[0]]
                    ) {
                      return (
                        <Fragment key={itemData[0]}>
                          <EditorComponent
                            description={description}
                            descriptionData={formData[itemData[0]]}
                            fieldname={itemData[0]}
                            title={itemData[0].split(/(?=[A-Z])/).join(" ")}
                          />
                        </Fragment>
                      );
                    } else if (itemData[1] === "quill" && !formInputValues.id) {
                      return (
                        <Fragment key={itemData[0]}>
                          <EditorComponent
                            description={description}
                            descriptionData={""}
                            fieldname={itemData[0]}
                            title={itemData[0].split(/(?=[A-Z])/).join(" ")}
                          />
                        </Fragment>
                      );
                    } else if (itemData[1] === "date") {
                      let dt = "";
                      if (formData[itemData[0]]) {
                        let date = new Date(formData[itemData[0]]);
                        let year = date.getFullYear();
                        let month = String(date.getMonth() + 1).padStart(
                          2,
                          "0"
                        );
                        let day = String(date.getDate()).padStart(2, "0");

                        dt = `${year}-${month}-${day}`;
                      }
                      return (
                        <FormGroup key={itemData[0]} className="px-3">
                          <Label
                            className="text-capitalize s-color"
                            for={itemData[0]}
                          >
                            {itemData[0].split(/(?=[A-Z])/).join(" ")}
                          </Label>
                          <Input
                            type="date"
                            id={itemData[0]}
                            required={itemData[2] === "require" ? true : false}
                            defaultValue={dt}
                            placeholder={itemData[0]
                              .split(/(?=[A-Z])/)
                              .join(" ")}
                            onChange={(e) =>
                              (formData[e.target.id] = e.target.value)
                            }
                          />
                        </FormGroup>
                      );
                    } else if (itemData[1] === "image") {
                      return (
                        <Fragment key={itemData[0]}>
                          <FileInputComponent
                            updateFileData={updateFileData}
                            fieldName={itemData[0]}
                            defaultVal={
                              formData[itemData[0]] ? formData[itemData[0]] : ""
                            }
                            fileType="image"
                          />
                        </Fragment>
                      );
                    } else if (itemData[1] === "file") {
                      return (
                        <Fragment key={itemData[0]}>
                          <FileInputComponent
                            updateFileData={updateFileData}
                            fieldName={itemData[0]}
                            defaultVal={
                              formData[itemData[0]] ? formData[itemData[0]] : ""
                            }
                            fileType="file"
                          />
                        </Fragment>
                      );
                    } else {
                      return null;
                    }
                  })}

                  <p className="text-danger text-center f-size-12">
                    {errorMsg}
                  </p>

                  <div className="d-grid py-2">
                    <Row className="px-2">
                      <Col className="text-center text-md-end">
                        {props.buttons &&
                          props.buttons.map((items, i) => {
                            return (
                              <Button
                                key={i}
                                type={
                                  items.type !== "cancel" ? "submit" : "button"
                                }
                                className="sp-btn-secondary rounded-0 px-3 px-md-5 mx-1 mx-md-2"
                                onClick={(e) => clickHandler(e, items.type)}
                              >
                                {items.title}
                              </Button>
                            );
                          })}
                      </Col>
                    </Row>
                  </div>
                </Form>
              </ModalBody>
            </Modal>
          </Col>
        </Row>
      ) : (
        <Row className="nonModal">
          <Col>
            <Form>
              {formField.map((item, i) => {
                var itemData = item.split("-");
                if (itemData[1] === "text") {
                  return (
                    <FormGroup key={itemData[0]} className="px-3">
                      <Label
                        className="text-capitalize t-color"
                        for={itemData[0]}
                      >
                        {itemData[0].split(/(?=[A-Z])/).join(" ")}
                        {itemData[2] === "require" ? (
                          <span className="text-danger">*</span>
                        ) : (
                          ""
                        )}
                      </Label>
                      <Input
                        type="text"
                        id={itemData[0]}
                        placeholder={itemData[0].split(/(?=[A-Z])/).join(" ")}
                        required={itemData[2] === "require" ? true : false}
                        defaultValue={
                          formData[itemData[0]] ? formData[itemData[0]] : ""
                        }
                        onChange={(e) =>
                          (formData[e.target.id] = e.target.value)
                        }
                      />
                    </FormGroup>
                  );
                } else if (itemData[1] === "textarea") {
                  return (
                    <FormGroup key={itemData[0]} className="px-3">
                      <Label
                        className="text-capitalize t-color"
                        for={itemData[0]}
                      >
                        {itemData[0].split(/(?=[A-Z])/).join(" ")}
                        {itemData[2] === "require" ? (
                          <span className="text-danger">*</span>
                        ) : (
                          ""
                        )}
                      </Label>
                      <Input
                        type="textarea"
                        id={itemData[0]}
                        placeholder={itemData[0].split(/(?=[A-Z])/).join(" ")}
                        required={itemData[2] === "require" ? true : false}
                        defaultValue={
                          formData[itemData[0]] ? formData[itemData[0]] : ""
                        }
                        onChange={(e) =>
                          (formData[e.target.id] = e.target.value)
                        }
                      />
                    </FormGroup>
                  );
                } else if (itemData[1] === "quill" && formData[itemData[0]]) {
                  return (
                    <div className="pb-2" key={itemData[0]}>
                      <EditorComponent
                        description={description}
                        descriptionData={formData[itemData[0]]}
                        fieldname={itemData[0]}
                        title={itemData[0].split(/(?=[A-Z])/).join(" ")}
                      />
                    </div>
                  );
                } else if (itemData[1] === "quill" && !formInputValues.id) {
                  return (
                    <div className="pb-2" key={itemData[0]}>
                      <EditorComponent
                        description={description}
                        descriptionData={""}
                        fieldname={itemData[0]}
                        title={itemData[0].split(/(?=[A-Z])/).join(" ")}
                      />
                    </div>
                  );
                } else if (itemData[1] === "date") {
                  let dt = "";
                  if (formData[itemData[0]]) {
                    let date = new Date(formData[itemData[0]]);
                    let year = date.getFullYear();
                    let month = String(date.getMonth() + 1).padStart(2, "0");
                    let day = String(date.getDate()).padStart(2, "0");

                    dt = `${year}-${month}-${day}`;
                  }
                  return (
                    <FormGroup key={itemData[0]} className="px-3">
                      <Label
                        className="text-capitalize t-color"
                        for={itemData[0]}
                      >
                        {itemData[0].split(/(?=[A-Z])/).join(" ")}
                      </Label>
                      <Input
                        type="date"
                        id={itemData[0]}
                        required={itemData[2] === "require" ? true : false}
                        defaultValue={dt}
                        placeholder={itemData[0].split(/(?=[A-Z])/).join(" ")}
                        onChange={(e) =>
                          (formData[e.target.id] = e.target.value)
                        }
                      />
                    </FormGroup>
                  );
                } else if (itemData[1] === "image") {
                  return (
                    <Fragment key={itemData[0]}>
                      <FileInputComponent
                        updateFileData={updateFileData}
                        fieldName={itemData[0]}
                        defaultVal={
                          formData[itemData[0]] ? formData[itemData[0]] : ""
                        }
                        fileType="image"
                      />
                    </Fragment>
                  );
                } else if (itemData[1] === "file") {
                  return (
                    <Fragment key={itemData[0]}>
                      <FileInputComponent
                        updateFileData={updateFileData}
                        fieldName={itemData[0]}
                        defaultVal={
                          formData[itemData[0]] ? formData[itemData[0]] : ""
                        }
                        fileType="file"
                      />
                    </Fragment>
                  );
                } else {
                  return null;
                }
              })}

              <p className="text-danger text-center f-size-12">{errorMsg}</p>

              <div className="d-grid py-2">
                <Row className="px-2">
                  <Col className="text-center text-md-end">
                    {props.buttons &&
                      props.buttons.map((items, i) => {
                        return (
                          <Button
                            key={i}
                            type={items.type !== "cancel" ? "submit" : "button"}
                            className="sp-btn-secondary rounded-0 px-3 px-md-5 mx-1 mx-md-2"
                            onClick={(e) => clickHandler(e, items.type)}
                          >
                            {items.title}
                          </Button>
                        );
                      })}
                  </Col>
                </Row>
              </div>
            </Form>
          </Col>
        </Row>
      )}
    </>
  );
};

export default FormElementComponent;
